@import url(https://fonts.googleapis.com/css2?family=Gloria+Hallelujah&family=Permanent+Marker&family=Sedgwick+Ave&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Architects+Daughter&display=swap);
body {
  margin: 0;
  background-color: #f7e0bd;
  font-family: 'Fira Sans Extra Condensed', sans-serif
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
html {
  background-color:#f7e0bd;
}


.home-body {
    background-color: #f7e0bd;
    align-items: center;
    text-align: center;
    font-family: 'Permanent Marker', cursive;
    color: #575656;
}
h1 {
    padding-top: 7%;
    font-size: 350%;
    color: rgb(14, 108, 151);
    padding-bottom: 7%;
}

.private {
    font-size: larger;
    color: rgb(9, 140, 248);
}

.safe {
    font-size: larger;
    color: green;
}

.to-join {
    font-family: 'Gloria Hallelujah', cursive;
    padding-top: 5%;
}

.label-text {
    font-size: small;
    background-color: rgb(51, 184, 69);
}

.warn {
    font-size: small;
    color: red;
    padding-bottom: 1%;
}

.no-room-id {
    padding-top: 1em;
    margin: auto;
    width: 60%;
    font-size: 0.6em;
    color: rgb(12, 73, 0);
}

.generate {
    margin-top: 1rem;
    margin-bottom: 50px;
}

.inside-pop-up {
    background-color: #f7e0bd;
    margin: auto;
    width: 80%;
}

.popup-content {
    font-family: 'Sedgwick Ave', cursive !important;
    padding: 10px !important;
    background-color: #f7e0bd !important;
    border: 0px !important;
    border-radius: 30px !important;
    width: 300px !important;
}

.popup-title {
    font-size: 1.5em;
    margin-bottom: 10px;
    margin-top: 15px;
}

.popup-name {
    font-size: 0.8em;
    margin-bottom: 10px;

}

.popup-submit {
    font-size: small;
}
.chat-design{
    font-family: 'Architects Daughter', cursive;
    background-color: #f7e0bd;
    padding: 50px;
}

.button-send {
    margin-top: auto;
    height: 60px;
    width: 10%;
    padding: 7px;
    margin-right: 1em !important;
}

.attach-send {
    margin-top: auto;
    height: 50px;
    width: 10%;
    margin-left: 0.4em !important;
    margin-right: 0.2em !important;
    padding: 5px;
}

.button-send:hover {
    margin-top: auto;
    height: 60px;
    width: 13%;
    padding: 10px;
    cursor: pointer;
}

.send-text {
    position: fixed;
    bottom: 0;
    right: 0;
    padding: 0px;
}

.send {
    margin-left: 2em !important;
    padding: 5px;
    min-height: 2vh;
    max-height: 25vh;
    overflow-y: scroll;
    width: 75%;
    background-color: #ffffff;
    word-wrap: break-word;
    word-break: break-all;
    overflow: scroll;
}

.messages-container {
    position: fixed;
    margin-top: 5%;
    width: 80%;
    height: 68%;
    margin-left: -2.667%;
    overflow-y: scroll;
}


.delete-room {
    font-size: 0.6em;
    margin-top: -10px;
}
.others {
    background-color: rgba(109, 192, 240, 0.685);
    padding: 5px;
    border-radius: 0.5em;
    margin-bottom: 2px;
    width: 70%;
}

.me {
    background-color: rgba(102, 165, 82, 0.603);
    margin-bottom: 2px;
    padding: 5px;
    border-radius: 0.5em;
    width: 70%;
    margin-left: 30%;
}

.name-container {
    font-size: smaller;
    text-shadow: 2em;
    margin-bottom: 0.5em;
    font-weight: bolder;
    word-break: break-all;
}

.message-container {
    font-size: small;
    word-break: break-all;
}

.image-container {
    margin-left: 1em;
    width: 70%;
}

.time-container {
    margin-top: 1em;
    margin-left: 85%;
    font-size: 0.6em;
    font-weight: bolder;
}
